.paginator {
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.paginator span {
    border: 1px solid #f1f5f8;
    padding: 2px;
    background-color: white;
}

.paginator span:hover {
    background-color: #07be92;
    color: white;
}

.paginator .selectedPage {
    font-weight: bold;
    background-color: #07be92;
    color: white;
    border: 2px solid #07be92;
}

.button {
    background-color: #b4fcea;
    border-radius: 5px;
}